// src/js/main.js - Enhanced main entry point with notification system
import { initRouter } from './router.js';
import { initDB } from './db.js';
import { Workbox } from 'workbox-window';
import { 
  initializeNotifications, 
  getNotificationStatus, 
  subscribePushNotification, 
  unsubscribePushNotification 
} from './notification.js';
import { getToken } from './utils/auth.js';

async function init() {
  await initDB();
  initRouter();
  setupInstallPrompt();
  await setupNotificationSystem();

  if ('serviceWorker' in navigator) {
    const wb = new Workbox('/sw.js');

    wb.addEventListener('installed', (event) => {
      if (event.isUpdate) {
        showUpdateNotification(wb);
      }
    });

    wb.addEventListener('waiting', (event) => {
      showUpdateNotification(wb);
    });

    wb.addEventListener('controlling', (event) => {
      window.location.reload();
    });

    wb.register();
  }
}

function setupInstallPrompt() {
  let deferredPrompt;
  const installBtn = document.getElementById('install-app-btn');

  window.addEventListener('beforeinstallprompt', (e) => {
    e.preventDefault();
    deferredPrompt = e;
    
    if (installBtn) {
      installBtn.style.display = 'flex';
    }
  });

  if (installBtn) {
    installBtn.addEventListener('click', async () => {
      if (deferredPrompt) {
        deferredPrompt.prompt();
        const result = await deferredPrompt.userChoice;
        if (result.outcome === 'accepted') {
          installBtn.style.display = 'none';
        }
        deferredPrompt = null;
      }
    });
  }

  // Hide install button if app is already installed
  if (window.matchMedia('(display-mode: standalone)').matches) {
    if (installBtn) {
      installBtn.style.display = 'none';
    }
  }
}

async function setupNotificationSystem() {
  try {
    // Initialize notification system
    await initializeNotifications();
    
    // Setup notification UI
    setupNotificationUI();
    
    console.log('Notification system setup completed');
  } catch (error) {
    console.error('Error setting up notification system:', error);
  }
}

function setupNotificationUI() {
  const header = document.querySelector('header nav ul');
  if (!header) return;

  // Create notification button container
  const notificationContainer = document.createElement('li');
  notificationContainer.id = 'notification-container';
  notificationContainer.style.display = 'none';
  
  const notificationBtn = document.createElement('button');
  notificationBtn.id = 'notification-btn';
  notificationBtn.className = 'notification-btn';
  notificationBtn.innerHTML = '<span class="btn-icon">üîî</span><span class="btn-text">Notifications</span>';
  
  notificationContainer.appendChild(notificationBtn);
  
  // Insert before install button
  const installBtn = document.getElementById('install-app-btn');
  if (installBtn && installBtn.parentElement) {
    header.insertBefore(notificationContainer, installBtn.parentElement);
  } else {
    header.appendChild(notificationContainer);
  }

  // Setup notification button click handler
  notificationBtn.addEventListener('click', handleNotificationToggle);
  
  // Update notification UI based on current status
  updateNotificationUI();
}

async function handleNotificationToggle() {
  const token = getToken();
  if (!token) {
    showToast('Please login first to enable notifications', 'warning');
    window.location.hash = '#/login';
    return;
  }

  const notificationBtn = document.getElementById('notification-btn');
  const originalContent = notificationBtn.innerHTML;
  
  try {
    notificationBtn.innerHTML = '<span class="spinner-small"></span> Processing...';
    notificationBtn.disabled = true;

    const status = await getNotificationStatus();
    
    if (status.isSubscribed) {
      // Unsubscribe
      await unsubscribePushNotification(token);
      showToast('Notifications disabled successfully', 'success');
    } else {
      // Subscribe
      await subscribePushNotification(token);
      showToast('Notifications enabled successfully', 'success');
    }
    
    // Update UI
    await updateNotificationUI();
    
  } catch (error) {
    console.error('Error toggling notifications:', error);
    showToast(`Error: ${error.message}`, 'error');
  } finally {
    notificationBtn.innerHTML = originalContent;
    notificationBtn.disabled = false;
  }
}

async function updateNotificationUI() {
  const notificationContainer = document.getElementById('notification-container');
  const notificationBtn = document.getElementById('notification-btn');
  
  if (!notificationContainer || !notificationBtn) return;

  try {
    const status = await getNotificationStatus();
    const token = getToken();
    
    // Show notification button only if user is logged in and notifications are supported
    if (token && status.isSupported) {
      notificationContainer.style.display = 'block';
      
      if (status.isSubscribed) {
        notificationBtn.innerHTML = '<span class="btn-icon">üîï</span><span class="btn-text">Disable</span>';
        notificationBtn.className = 'notification-btn subscribed';
        notificationBtn.title = 'Click to disable notifications';
      } else if (status.canSubscribe) {
        notificationBtn.innerHTML = '<span class="btn-icon">üîî</span><span class="btn-text">Enable</span>';
        notificationBtn.className = 'notification-btn unsubscribed';
        notificationBtn.title = 'Click to enable notifications';
      } else {
        notificationBtn.innerHTML = '<span class="btn-icon">üö´</span><span class="btn-text">Blocked</span>';
        notificationBtn.className = 'notification-btn blocked';
        notificationBtn.title = 'Notifications are blocked. Please enable in browser settings.';
        notificationBtn.disabled = true;
      }
    } else {
      notificationContainer.style.display = 'none';
    }
  } catch (error) {
    console.error('Error updating notification UI:', error);
    notificationContainer.style.display = 'none';
  }
}

function showUpdateNotification(wb) {
  const updateBanner = document.createElement('div');
  updateBanner.className = 'update-banner';
  updateBanner.innerHTML = `
    <div class="update-content">
      <span>üîÑ App update available!</span>
      <button id="update-btn" class="btn btn-primary">Update Now</button>
      <button id="dismiss-update" class="btn btn-outline">Later</button>
    </div>
  `;
  
  document.body.appendChild(updateBanner);
  
  document.getElementById('update-btn').addEventListener('click', () => {
    wb.messageSkipWaiting();
    updateBanner.remove();
  });
  
  document.getElementById('dismiss-update').addEventListener('click', () => {
    updateBanner.remove();
  });
}

function showToast(message, type = 'success') {
  const toast = document.createElement('div');
  toast.className = `toast-notification ${type}`;
  toast.textContent = message;
  
  document.body.appendChild(toast);
  
  setTimeout(() => {
    toast.classList.add('show');
  }, 100);
  
  setTimeout(() => {
    toast.classList.remove('show');
    setTimeout(() => {
      if (document.body.contains(toast)) {
        document.body.removeChild(toast);
      }
    }, 300);
  }, 3000);
}

// Update notification UI when auth state changes
window.addEventListener('hashchange', () => {
  setTimeout(updateNotificationUI, 100);
});

// Listen for storage changes (login/logout in other tabs)
window.addEventListener('storage', (e) => {
  if (e.key === 'token') {
    setTimeout(updateNotificationUI, 100);
  }
});

// Listen for online/offline events
window.addEventListener('online', () => {
  showToast('Connection restored', 'success');
});

window.addEventListener('offline', () => {
  showToast('You are offline. Some features may be limited.', 'warning');
});

init();